* {
    margin: 0;
    box-sizing: border-box;
    .main-background {
        display: flex;
        align-items: center;
        flex-direction: column;
        height: 100%;
        width: 100%;
        background-color: $background-color;
        color: $font-color;
        font-family: "Montserrat", sans-serif;
    }
}
.long-line {
    width: 90%;
    background-color: $font-color;
    height: 1px;
    margin-top: 1.5rem;
    margin-bottom: 1.5rem;
}

.short-line {
    width: 20rem;
    background-color: $font-color;
    height: 1px;
    margin-top: 1.5rem;
}
